.decorline {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &::before {
    content: '';
    display: none;
    width: 100%;
    height: 1px;
    background-color: $textcolor;
    opacity: 0.2;
  }

  &::after {
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background-color: $textcolor;
    opacity: 0.2;
  }

  &__text {
    display: block;
    padding-right: 14px;
    white-space: nowrap;
  }

  &--marginbtm {
    margin-bottom: 30px;

    @include mw2 {
      margin-bottom: 25px;
    }

    @include mw3 {
      margin-bottom: 22px;
    }

    @include mw4 {
      margin-bottom: 17px;
    }

    @include mw5 {
      margin-bottom: 12px;
    }
  }

  @include mw3 {
    &::before {
      display: block;
    }

    &__text {
      padding: 0 14px;
    }
  }
}