.hero {
  position: relative;
  display: block;
  width: 100%;
  min-height: 334px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(255, 255, 255, 0.1);

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: $mainbgcolor;
    opacity: 0.15;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10.5% 5px 17%;
    min-height: 330px;
    max-height: 575px;
  }

  &__title {
    margin-bottom: 23px;
    text-align: center;
    text-shadow: 0 2px 2px rgba($color: #000, $alpha: 0.45);
  }

  &__descr {
    max-width: 444px;
    line-height: 1.15;
    text-align: center;
    text-shadow: 0 2px 2px rgba($color: #000, $alpha: 0.45);
  }

  &--home {
    background-image: url('../images/main-bg.jpg');
  }

  &--supervisory-partnerships {
    background-image: url('../images/supervisory-partnerships/main-bg.jpg');
  }
}
