
.task {
  &__title {
    margin-bottom: 23px;

    @include mw2 {
      margin-bottom: 20px;
    }

    @include mw3 {
      margin-bottom: 15px;
    }

    @include mw4 {
      margin-bottom: 7px;
    }
  }

  &__item {
    position: relative;
    padding-left: 23px;
    color: $textcolor;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 10px;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      background-color: $anakiwa;
    }

    &:not(:last-child) {
      margin-bottom: 12px;

      @include mw3 {
        margin-bottom: 8px;
      }

      @include mw4 {
        margin-bottom: 5px;
      }
    }
  }
}